body {
  background-color: #fefefe;
  padding: 20px;
}

header {
  color: palegoldenrod;
}

.App {

  margin: 30px auto;
  width: auto;
  max-width: 700px;
  padding: 10px 30px;
  padding-bottom: 30px;
  background-color: #ffffff;
  box-shadow: 0 5px 10px 1px #bbb;
  border-radius: 3px;
  color: #fefefe;
  background-color: #2e2e2e;
}

.footer {
  margin: 30px auto;
  width: auto;
  max-width: 700px;
  padding: 10px 30px;
  text-align: right;
}

.redtxt {
  color: red;
}

hr {
  margin: 25px 0;
  border-color: #eee;
  border-width: 0.5px;
}